@import "images.css";

html,
body {
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
    Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  background-color: #e0e0e0;
}

a {
  color: inherit;
  text-decoration: none;
}

* {
  box-sizing: border-box;
  border: 1px solid black;
}

img {
  z-index: 3;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: center;
}

li {
  font-size: 1.5rem;
  border-bottom: 1px solid grey;
}

li span {
  margin-left: 12%;
}

li img {
  width: 35%;
}

.section {
  display: grid;
  min-height: 96.5vh;
  min-width: 99.9vw;
}
.section-input {
  display: grid;
  min-height: 96.5vh;
  min-width: 99.9vw;
  justify-content: center;
  align-items: center;
}

.section_photos {
  height: auto;
}

.heading-input {
  margin-bottom: 20%;
  height: 20%;
  background-color: rgba(255, 255, 255, 95%);
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  text-align: center;
  border-radius: 10px;
}

.heading {
  height: 100%;
  width: 100%;
  display: grid;
  justify-content: center;
  align-items: center;
}

.heading p {
  margin-bottom: 20%;
}

.nav {
  height: 5rem;
  background-color: rgb(237, 234, 222);
}

.nav-center {
  margin: 0 auto;
  max-width: 1170px;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: center;
  align-items: center;
}

.nav-header div {
  font-size: 2rem;
  font-weight: 600;
}

.nav-center ul {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 5%;
  font-size: 0.8rem;
  font-weight: 600;
  text-decoration: none;
  height: 100%;
  width: 100%;
}

.nav-center ul li {
  text-decoration: none;
  border-bottom: none;
  font-size: 1.1rem;
}

.nav-center ul li a:hover {
  color: rgba(0, 0, 0, 0.5);
}

.heading h1 {
  font-size: 4rem;
}

.link-front .photos_input_form {
  height: 10%;
  width: 100%;
  margin-top: 20%;
}

.photo-input {
  font-size: 1.4rem;
  height: 40%;
  width: 30%;
}

.album-link-container {
  width: 100%;
}

.album-link {
  width: 100%;
  cursor: pointer;
}

.album-link div {
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
}

.album-description {
  background-color: rgba(255, 255, 255, 80%);
  margin-top: 10%;
  width: 100%;
  height: 50vh;
}

.album-description h3 {
  cursor: pointer;
  text-align: center;
  font-size: 2rem;
  width: 100%;
  height: 8vh;
}

.album-description p {
  margin: 0 auto;
  margin: 1rem;
  width: 90%;
}

.album-description div {
  text-align: center;
  width: 100%;
}

.submit {
  font-size: 1rem;
  width: 20%;
}

.container-cart_button {
  margin-left: 42%;
}

.container-main_products {
  grid-column: span 4;
  margin-top: 5%;
}

.main_products {
  grid-column: span 2;
  display: grid;
  grid-template-columns: 20vw 20vw 20vw 20vw;
  column-gap: 5vw;
  justify-content: center;
  align-items: center;
}

.container-single_main_product {
  grid-column: span 2;
  margin: 0 auto;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  background-color: white;
}

.single_main_product {
  width: 60%;
}

.add_main_product_to_cart {
  margin-left: 10px;
  width: 70%;
}

.container-other_products {
  display: none;
  left: 0;
  right: 0;
  margin: 30vh auto;
  width: 90vw;
  z-index: 2;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  background-color: white;
}

.show-other_products_modal {
  display: block;
  position: absolute;
}

.close_product_button {
}

.container-single_other_product {
  background-color: white;
  text-align: center;
}

.single_other_product {
  font-size: 2rem;
}

.container-photos {
  grid-column: span 4;
  margin: 10% auto 0 auto;
}

.photos {
  display: grid;
  grid-template-columns: 10vw 10vw 10vw 10vw;
  column-gap: 10vw;
  row-gap: 5vh;
}

.photos-single {
  grid-column: span 3;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  background-color: white;
}

.img-single {
  padding-left: 10px;
}

.container-cart {
  display: none;
  left: 0;
  right: 0;
  margin: 30vh auto;
  width: 90vw;
  background-color: rgba(255, 255, 255, 1);
  z-index: 2;
  text-align: right;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.show-cart {
  display: block;
  position: absolute;
}

#cart-total {
  font-size: 2rem;
  padding-right: 10%;
}

.payment_modal {
  display: none;
  left: 0;
  right: 0;
  margin: 30vh auto;
  width: 90vw;
  background-color: rgba(255, 255, 255, 1);
  z-index: 3;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  justify-content: center;
}

.show_payment_modal {
  display: flex;
  position: absolute;
}

.flex-form {
  display: flex;
  flex-direction: column;
}

.date-cvc {
  display: block;
}

.flex-container {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.flex-item_1 {
}

.flex-item_2 {
}

.flex-button {
}
/* medium: 700px
  large: 900px
  xlarge: 1440px */

/* tablet */

@media screen and (min-width: 700px) {
  .album-link-container {
    width: 80%;
    margin: 0 auto;
  }

  .album-description {
    margin: 5% auto 0 auto;
    width: 80%;
    height: 50vh;
  }

  .main_products {
    column-gap: 3vw;
    padding-left: 0;
  }

  .add_main_product_to_cart {
    margin-left: 10px;
    width: 50%;
  }

  .container-other_products {
    width: 60vw;
  }

  .container-cart {
    width: 70vw;
  }

  .photos-single {
    grid-column: span 2;
  }

  .payment_modal {
    width: 70vw;
  }

  .flex-form {
    flex-direction: row;
    gap: 15px;
  }

  .payment_form_heading {
    display: flex;
    flex-direction: row;
  }

  .flex-container {
    display: flex;
    flex-direction: row;
    gap: 50%;
  }
}

/* big laptops */

@media screen and (min-width: 900px) {
  .album-link-container {
    width: 60%;
    height: 40%;
    margin: 5% auto;
  }

  .album-description {
    margin: 8% auto 0 auto;
    height: 40%;
    width: 40%;
  }

  .album-description p {
    font-size: 0.8rem;
  }

  .main_products {
    column-gap: 0;
  }

  .container-other_products {
    width: 40vw;
  }

  .container-cart {
    width: 50vw;
  }

  .payment_modal {
    width: 50vw;
  }

  .photos-single {
    grid-column: span 2;
    width: 70%;
  }

  .nav-header div {
    font-size: 2.5rem;
    font-weight: 600;
  }

  .nav-center ul {
    font-size: 1rem;
  }
}

@media screen and (min-width: 1440px) {
  .main_products {
    display: grid;
    grid-template-columns: 20vw 20vw 20vw 20vw;
    column-gap: 5vw;
  }

  .container-single_main_product {
    grid-column: span 2;
  }

  .photos {
    grid-template-columns: 16vw 16vw 16vw 16vw;
    column-gap: 2vw;
  }

  .container-other_products {
    width: 30vw;
  }

  .container-cart {
    width: 30vw;
  }

  .payment_modal {
    width: 30vw;
  }

  .photos-single {
    grid-column: span 1;
  }
}

@media screen and (min-width: 2560px) {
  .main_products {
    column-gap: 0;
  }

  .photos-single {
    grid-column: span 1;
  }
}

/* CSS */
.link-front-page {
  background-color: rgba(255, 255, 255, 95%);
  border: 0 solid #e2e8f0;
  border-radius: 1.5rem;
  box-sizing: border-box;
  color: #0d172a;
  cursor: pointer;
  display: inline-block;
  font-family: "Basier circle", -apple-system, system-ui, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1.1rem;
  font-weight: 600;
  line-height: 1;
  padding: 1rem 1.6rem;
  text-align: center;
  text-decoration: none #0d172a solid;
  text-decoration-thickness: auto;
  transition: all 0.1s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0px 1px 2px rgba(166, 175, 195, 0.25);
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  margin-top: 10%;
}

.link-front-page:hover {
  background-color: rgba(255, 255, 255, 80%);
}

@media (min-width: 768px) {
  .link-front-page {
    font-size: 1.5rem;
    padding: 2.5rem 4.5rem;
  }
}
